*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
body{
  overflow-x: hidden;
  display: block;
  justify-content: center;
}


.itemNoticia {
  display: flex;
  align-items: center;
  border: 2px solid black;
  border-radius: 10px;
  padding: 10px;
}
.itemNoticia picture{
  flex: 0 0 auto;
  max-width:180px;
  margin-right: 1rem;
  
}
.itemNoticia picture img{
  max-width:180px;
  width: 180px;
  object-fit: cover;
  
}

.titulo{
  color: red;
  font-size: large;
  font-weight: bolder;
}
.subtitulo{
  font-size: larger;
}
button{
  margin-top: 1rem;
  padding: 0.75rem 1rem 0.75rem 1rem;
  cursor: pointer;
}